import React, { useEffect, useState } from "react";
import Labelwithdescription from "../../Components/labelwithdescription/Labelwithdescription";
import Inputtextbox from "../../Components/inputtextbox/Inputtextbox";
import HeadingwithDescription from "../../Components/HeadingwithDescription/HeadingwithDescription";
import Submitbutton from "../../Components/submitbutton/Submitbutton";
import { useNavigate } from "react-router-dom";
import { LOGIN_API } from "../../API_Requests";
import Cookies from "js-cookie";

export default function Loginpage() {
  const [error, setError] = useState("");
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  let navigate = useNavigate();

  const HandleLogin = (e) => {
    e.preventDefault();
    LOGIN_API(username, password).then((res) => {
      // console.log(res)
      if (res.status == 401) {
        setError("Wrong Credentials");
      } else if (res.status == 200) {
        Cookies.set("token", res.data.token, { expires: 7 });
        navigate("/dashboard");
      }
    });
  };

  return (
    <div className="flex justify-center h-screen items-center ">
      <form
        className="border border-gray-400 bg-primary rounded-md p-20 "
        onSubmit={HandleLogin}
      >
        <div>
          <HeadingwithDescription
            label="CPGS | Liquidlab "
            description="Camera parking Guidence System"
          />
        </div>
        <p className="text-red-400">{error}</p>
        <div>
          <Inputtextbox
            label="Username"
            description="Enter the CPGS Console username for this device  ( 1001 )"
            placeholder="Username"
            updatedata={setUsername}
          />
          <Inputtextbox
            label="Password"
            description="Enter the Password"
            placeholder="Password"
            updatedata={setPassword}
            type="password"
          />
        </div>
        <div className="flex justify-end">
          <Submitbutton text="Login" />
        </div>
      </form>
    </div>
  );
}
