import React, { useEffect, useState } from 'react'
import Inputtextbox from '../../Components/inputtextbox/Inputtextbox'
import Labelwithdescription from '../../Components/labelwithdescription/Labelwithdescription'
import { GET_ACCOUNT_INFO, UPDATE_ACCOUNT_INFO } from '../../API_Requests'

export default function Accounts() {

    const [data, setData] = useState({
        new_username: "",
        old_username: "",
        old_password: "",
        new_password: "",
        device_id: "",
    })

    const [alert, setAlert] = useState("")

    const HandleSubmit = (event) => {
        console.log(data)

        event.preventDefault()
        if (data.new_username === "") {
            setAlert("New Username cannot be Empty")
        }


        else if (data.old_password === "") {
            setAlert("Old password cannot be Empty")
        }

        else if (data.new_password === "") {
            setAlert("New password cannot be Empty")
        }

        else {
            setAlert("")
            UpdateAccountInfo(data)
        }

    }

    const UpdateAccountInfo = async () => {
        let info = await UPDATE_ACCOUNT_INFO(data)
        setData({
            ...data,
            old_username: info.username,
            old_password: info.password,
            device_id: info.device_id,
        })
    }

    const getAccountInfo = async () => {
        let info = await GET_ACCOUNT_INFO()
        setData({
            ...data,
            old_username: info.username,
            old_password: info.password,
            device_id: info.device_id,
        })
    }

    useEffect(() => {
        // Fetch current user account details
        getAccountInfo()

    }, [])

    return (
        <div className=''>
            <Labelwithdescription label="Accounts" description="Manage Login credentials Here" />
            <div className=''>
                <form onSubmit={(event) => HandleSubmit(event)}>
                <div className='mr-2 my-2'>
                            <p className='text-gray-600 '>Device ID</p>
                            <input type="number" id='textinput' className='border-gray-300 border rounded-lg py-3 px-5  w-full' placeholder="New username" onChange={(e) => {
                                setData({ ...data, device_id: e.target.value })
                            }} name='new_username' value={data.device_id} />
                        </div>
                    <div className='my-5 w-full flex'>




                      
                        <div>
                            <div className='mr-2 my-2'>
                                <p className='text-gray-600 '>Current UserName</p>
                                <input type="text" id='textinput' className='border-gray-300 border rounded-lg py-3 px-5  w-full' placeholder="New username" onChange={(e) => {
                                    setData({ ...data, old_username: e.target.value })
                                }} name='new_username' value={data.old_username} />
                            </div>

                            <div className='mr-2 my-2'>
                                <p className='text-gray-600 '>Current Password</p>
                                <input type="password" id='textinput' className='border-gray-300 border rounded-lg py-3 px-5  w-full' placeholder="Old Password" onChange={(e) => {
                                    setData({ ...data, old_password: e.target.value })
                                }} name='old_password' value={data.old_password

                                } />
                            </div>

                        </div>

                        <div>
                            <div className='my-2'>
                                <p className='text-gray-600 '>New UserName</p>
                                <input type="text" id='textinput' className='border-gray-300 border rounded-lg py-3 px-5  w-full' placeholder="New username" onChange={(e) => {
                                    setData({ ...data, new_username: e.target.value })
                                }} name='new_username' value={data.new_username} />
                            </div>
                            <div className='my-2'>
                                <p className='text-gray-600 '>New Password</p>
                                <input type="password" id='textinput' className='border-gray-300 border rounded-lg py-3 px-5  w-full' placeholder="New Password" onChange={(e) => {
                                    setData({ ...data, new_password: e.target.value })
                                }} name='new_password' value={data.new_password} />
                            </div>
                        </div>
                    </div>

                    <div className='w-full'>
                        <button className='bg-primary p-3 px-10 w-full rounded-lg text-white my-5 cursor-pointer ' type='submit'>Save</button>
                    </div>

                    <p className='text-red-700 font-bold'>{alert}</p>

                </form>
            </div>
        </div>
    )
}
