import React, { useEffect, useState } from "react";
import Labelwithdescription from "../../Components/labelwithdescription/Labelwithdescription";
import Toggle from "react-toggle";
import {
  GET_MANUAL_CALIBRATE_FRAME_REQUEST,
  RESET_SPACE_COORDINATES,
  STOP_STREAM,
  UPDATE_SPACE_COORDINATES,
  GET_ALL_DEVICE_IDS,
  CLICK_BACKGROUND_MASK,
} from "../../API_Requests";

export default function Calibration() {
  const [calibrationData, setCalibrationData] = useState({
    dectection_value: 0,
    threshold_value: 0,
    external_light_status: false,
  });
  const [devices, setDevices] = useState([]);
  const [selectedDeviceId, setSelectedDeviceId] = useState("all");

  useEffect(() => {
    GET_MANUAL_CALIBRATE_FRAME_REQUEST();
  });

  useEffect(() => {
    const fetchDevices = async () => {
      try {
        const deviceIds = await GET_ALL_DEVICE_IDS();
        setDevices(deviceIds);
      } catch (error) {
        console.error("Error fetching devices:", error);
      }
    };
    fetchDevices();
  }, []);

  console.log(devices);
  

  const handleMask = () => {
    try {
      CLICK_BACKGROUND_MASK(selectedDeviceId);
    } catch (error) {
      console.error("Error fetching devices:", error);
    }
  };

  const handleImageClick = (event) => {
    const img = event.target;
    const rect = img.getBoundingClientRect();
    const x = event.clientX - rect.left;
    const y = event.clientY - rect.top;

    // console.log(`Clicked coordinates: (${x}, ${y})`);
    UPDATE_SPACE_COORDINATES(x, y);
  };

  return (
    <div>
      {/* <div className='my-5'>
        <Labelwithdescription label="Dectection Value" />
        <input type="text" value={calibrationData.dectection_value} className='border-gray-300 border rounded-lg py-3 px-5  w-full' placeholder="" onChange={(e) => {
          setCalibrationData({ ...calibrationData, dectection_value: e.target.value })
        }} />
      </div>

      <div className='my-5'>
        <Labelwithdescription label="External Light" />
        <div>
          <label>Check to Turn it On</label>
          <input type='checkbox' onChange={() => setCalibrationData({ ...calibrationData, })} className='mx-5' />

        </div>
      </div> */}
      <Labelwithdescription
        label="Calibration Setup"
        description={"Click the corners of each space for Space Marking"}
      />

      <div className="flex  flex-col justify-center ">
        <img
          id="manualCalibrate_stream"
          className="rounded-xl w-full"
          alt="Video Stream"
          onClick={(event) => handleImageClick(event)}
        />
        <button
          id="calibrationStreamLoader"
          className="p-3 bg-primary text-white"
        >
          Loading
        </button>
        <div className="flex justify-between">
          <div className="flex">
            <button
              className="text-white bg-blue-500 rounded-md px-10 py-5 hover:bg-blue-900"
              onClick={GET_MANUAL_CALIBRATE_FRAME_REQUEST}
            >
              Get Next Shot
            </button>
            <button
              className="text-white bg-red-600 rounded-md px-10 py-5 ml-5 hover:bg-red-900"
              onClick={RESET_SPACE_COORDINATES}
            >
              Reset
            </button>
          </div>

          <div className="flex items-center">
            <select
              className="w-[200px] border border-black h-10"
              name="device_id"
              id="device_id"
              value={selectedDeviceId}
              onChange={(e) => setSelectedDeviceId(e.target.value)}
            >
              <option value="all">All</option>
              {devices?.map((deviceId) => (
                <option key={deviceId} value={deviceId}>
                  {deviceId}
                </option>
              ))}
            </select>
            <button
              className="text-white bg-emerald-600 rounded-md px-10 py-5 ml-5 hover:bg-emerald-900"
              onClick={handleMask}
            >
              Background Mask
            </button>
          </div>
        </div>
      </div>

      {/* <div className='flex justify-end'>
        <button className='bg-primary p-3 text-white w-20 rounded-lg'>Save</button>

      </div> */}
    </div>
  );
}
