// SEND NEW SPACE COORDINATES TO THE SERVER THROUGHT SOCKET
export const GET_MONITOR_COUNT = async () => {
  let token = CheckUser();
  let spaceViewContainer = document.getElementById("spaceViewContainer");
  let data = {
    task: "GET_MONITOR_COUNT",
    data: "",
    token: token,
  };
  let res = await axios.post(`${cpgsServerIp}/monitor_handler`, data, {
    headers: header,
  });
  let NoOfMonitors = res.data.data;
  for (let space = 0; space < NoOfMonitors; space++) {
    const monitor = document.getElementById(`liveMonitor${space}`);
    // Create Monitors
    if (!monitor) {
      const div = document.createElement("div");
      div.className = "flex-col";
      div.innerHTML = `
      <div class='border-2  bg-gray-100  text-white rounded-md mr-5' >
          <img id="liveLicensePlate${space}" alt="licensePlate" class=" border-primary border-2  rounded-lg" />
          <img id="liveMonitor${space}" alt="live Stream" class="border border-black rounded-lg" />

          <p class= "font-bold flex justify-center text-center">SPACE ID ${space}</p>
          </div>
      `;
      spaceViewContainer.appendChild(div);
    }
  }

  return NoOfMonitors;
};

// GET THE FRAMES OF MONITORING VIEWS
export const GET_MONITOR_VIEWS = () => {
  return new Promise(async (resolve, reject) => {
    try {
      let token = CheckUser();
      let data = {
        task: "GET_MONITOR_VIEWS",
        data: "",
        token: token,
      };
      let res = await axios.post(`${cpgsServerIp}/monitor_handler`, data, {
        headers: header,
      });

      let views = res.data.data;
      if (res.status === 200) {
        resolve(res);
        for (let space = 0; space < views.length; space++) {
          const monitorData = document.getElementById(
            `liveLicensePlate${space}`
          );
          const monitorLicensePlate = document.getElementById(
            `liveMonitor${space}`
          );
          if (monitorData) {
            if (views[space]["spaceFrame"] != "") {
              monitorData.src = views[space]["spaceFrame"];
            }
          }
          if (monitorLicensePlate && views[space]["licensePlate"] !== "") {
            monitorLicensePlate.src = views[space]["licensePlate"];
          }
        }
      }
    } catch (error) {
      reject(error);
    }
  });
};

// FRAMES WHILE CALIBRATING THE FRAMES
export const GET_MANUAL_CALIBRATE_FRAME_REQUEST = async () => {
  let token = CheckUser();
  const imgElement = document.getElementById("manualCalibrate_stream");
  const calibrationStreamLoader = document.getElementById(
    "calibrationStreamLoader"
  );
  let data = {
    task: "GET_CAMERA_VIEW_WITH_COORDINATES",
    data: "",
    token: token,
  };
  let res = await axios.post(`${cpgsServerIp}/calibrate_handler`, data, {
    headers: header,
    responseType: "arraybuffer", // Ensure response is binary
  });

  // Convert ArrayBuffer to Base64
  function arrayBufferToBase64(buffer) {
    let binary = "";
    let bytes = new Uint8Array(buffer);
    for (let i = 0; i < bytes.length; i++) {
      binary += String.fromCharCode(bytes[i]);
    }
    return btoa(binary);
  }

  // Generate Base64 string
  let base64String = arrayBufferToBase64(res.data);
  let dataUrl = `data:image/jpeg;base64,${base64String}`;

  // console.log(dataUrl);

  // Show image
  imgElement.style.display = "block";
  calibrationStreamLoader.style.display = "none";
  imgElement.src = dataUrl;
};

