import React from 'react'
import { useNavigate } from 'react-router-dom'

export default function SelectableButton({text, goto, selected, onselect}) {
    let navigate = useNavigate()
  return (
    <div>
    {selected?<button onClick={onselect} className='bg-blue-500 rounded-md font-semibold text-white px-3 py-1 w-full text-start'>
        {text}
    </button>:
    <button onClick={onselect} className='rounded-md text-white px-5 py-1 w-full text-start'>
        {text}
    </button>}
    </div>
    
  )
}
