import React, { useEffect, useState } from "react";
import Sidebar from "../../Components/SideBar/Sidebar";
import Calibration from "../Calibration/Calibration";
import Networksetup from "../Networksetup/Networksetup";
import Spacesetup from "../Spacesetup/Spacesetup";
import {
  Route,
  BrowserRouter as Router,
  Routes,
  useNavigate,
} from "react-router-dom";
import AutoCoordinateFinder from "../AutoCoordinateFinder/AutoCoordinateFinder";
import ManualCoordinateFinder from "../ManualCoordinateFinder/ManualCoordinateFinder";
import LiveStream from "../LiveStream/Livestream";
import Accounts from "../Accounts/Accounts";
import MonitoringStation from "../MonitoringStation/MonitoringStation";
import DynamicToggle from "../../Components/DynamicToggle/DynamicToggle";
import { GET_MODE, REBOOT_SYSTEM, UPDATE_MODE } from "../../API_Requests";
import LiveDashboard from "../LiveDashboard/LiveDashboard";
import SaveModal from "../../Components/Modal/SaveModal";
import Cancelbutton from "../../Components/cancelbutton/Cancelbutton";
import Confirmbutton from "../../Components/confirmbutton/Confirmbutton";

export default function Dashboard() {
  const [content, setContent] = useState(0);
  // const navigate = useNavigate()
  const [islive, setisLive] = useState();
  const [rebootModal, setrebootModal] = useState(false);

  const changeMode = async (name, isLive) => {
    console.log("name,islive", name, islive);
    const mode = await UPDATE_MODE(isLive);
    if (mode === "live") {
      setisLive(true);
    } else if (mode === "config") {
      setisLive(false);
      setContent(0);
    }
  };

  const getModeConfiguration = async () => {
    let mode = await GET_MODE();
    console.log(mode);
    if (mode === "live") {
      setisLive(true);
    } else if (mode === "config") {
      setisLive(false);
    }
  };

  useEffect(() => {
    getModeConfiguration();
  }, []);

  return (
    <div className="">
      {/* <Loginpage/> */}

      <Sidebar setContent={setContent} mode={islive} />

      <div className=" w-full ">
        <div className="flex justify-end bg-secondary px-10 pt-10">
          {/* <p>here : {islive ? " live" : " config"}</p> */}
          <DynamicToggle
            label="Change Mode"
            name="mode"
            onChange={changeMode}
            activeLabel="live"
            inactiveLabel="config"
            checked={islive}
          />
        </div>

        <div className="px-10 flex justify-center">
          {content == 0 ? (
            <Accounts />
          ) : content == 1 ? (
            <MonitoringStation setContent={setContent} />
          ) : content == 2 ? (
            <Networksetup saveModal={setrebootModal} />
          ) : content == 3 ? (
            <Calibration />
          ) : content == 4 ? (
            <LiveDashboard />
          ) : content == 5 ? (
            <Accounts />
          ) : content == 6 ? (
            <AutoCoordinateFinder setContent={setContent} />
          ) : content == 7 ? (
            <ManualCoordinateFinder setContent={setContent} />
          ) : (
            ""
          )}
        </div>
      </div>
      <div>
        {rebootModal ? (
          <div className="absolute inset-0 flex justify-center items-center">
            <div className="modal-overlay bg-white border border-gray-200 rounded-lg shadow-xl">
              <div
                className="modal-content p-10  "
                onClick={(e) => e.stopPropagation()}
              >
                <div className="text-center text-gray-600 ">
                  <h1 className="font-semibold text-3xl p-5">Warning!</h1>
                  <p className="">You Changes Have been Saved Successfully, </p>
                  <p>You Have to Reboot the system to Make the changes Live</p>
                </div>
                <div className="flex justify-between mt-7">
                  <Cancelbutton
                    call={() => setrebootModal(false)}
                    text="Later"
                  />
                  <Confirmbutton
                    call={() => REBOOT_SYSTEM()}
                    text="Reboot Now"
                  />
                </div>
                {/* <h2>Saved</h2>
                  <button onClick={onClose}>Close</button> */}
              </div>
            </div>
          </div>
        ) : (
          ""
        )}
      </div>
    </div>
  );
}
